.header{
    display: flex;
    text-align: center;
    align-items: center;
    border: 1px solid;
    width: 290px;
    height: 20px;
    justify-content: space-between;
    margin: 20px 0;
    padding: 0px 10px;

}
.header button{
    background-color: white;
    border: none;
}
.header input[type=checkbox]:checked {
    color: #ff0000;
  }
.header-green {
    display: flex;
    text-align: center;
    align-items: center;
    border: 1px solid;
    width: 290px;
    height: 20px;
    justify-content: space-between;
    margin: 20px 0;
    padding: 0px 10px;
    text-decoration: line-through;
    color: rgb(148, 148, 148);
}